import React from 'react';
import Home from '../Home'
import Art from '../Art'

class Master extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            home: true,
            gallery: false,
        }
    }

    startGallery = () => {
        this.setState({ home: false, })
        this.setState({ gallery: true, })
    }

    checkState = () => {
        if (this.state.home === true) {
            return <Home startGallery={this.startGallery} />
        } else { return <Art /> }
    }

    render() {
        return (
            <div> {this.checkState()}</div >
        )
    }
}
export default Master;