import React from 'react';
import './Styles/Art.css';
import Axios from 'axios';
import Letter from './Components/Letter'
import Zoom from './Components/Zoom'

class Art extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      isloaded: false,
      imageURL1: 'one',
      imageURL2: 'two',
      imageURL3: 'three',
      imageURL4: 'four',
      imageURL5: 'five',
      imageURL6: 'six',
      imageURL7: 'seven',
      imageURL8: 'eight',
      imageURL9: 'nine',
      imageURL10: 'ten',
      whichClick: "imageURL0",
      displayZoom: false,
    };
  }

  componentDidMount() {
    Axios.get('https://api.harvardartmuseums.org/image', {
      params: {
        apikey: 'b21678a9-dae3-4cdc-a3dd-e1ff27fc524b',
        page: 1,
        sort: 'random'
      }
    }
    )
      .then(res => {
        console.log(res)
        this.setState({ imageURL1: res.data.records[0] })
        this.setState({ imageURL2: res.data.records[1] })
        this.setState({ imageURL3: res.data.records[2] })
        this.setState({ imageURL4: res.data.records[3] })
        this.setState({ imageURL5: res.data.records[4] })
        this.setState({ imageURL6: res.data.records[5] })
        this.setState({ imageURL7: res.data.records[6] })
        this.setState({ imageURL8: res.data.records[7] })
        this.setState({ imageURL9: res.data.records[8] })
        this.setState({ imageURL10: res.data.records[9] })

        this.setState({ isloaded: true })

        // console.log(this.state.imageURL)
        // console.log(this.state.imageURL.date)
        // console.log(this.state.imageURL.baseimageurl)
        // if (this.state.isloaded === true) {
        //   this.setState({ date: this.state.imageURL.date });
        //   this.setState({ id: this.state.imageURL.id });
        //   this.setState({ description: this.state.imageURL.description });
        //   this.setState({ copyright: this.state.imageURL.copyright });
        //   this.setState({ technique: this.state.imageURL.technique });
        // }
      }
      ).catch(err => console.log(err));
  }


  turnOff = () => {
    this.setState({ displayZoom: false })
  }

  checkInfo = () => {
    if (this.state.isloaded === true && this.state.displayZoom === true) {
      const info = this.state.whichClick;
      console.log(info);
      console.log(this.state[info])
      return <Zoom display={this.state[info]} turnOff={this.turnOff} />
    }
  }


  render() {
    return (
      <div className="fullspace-master-div">
        <div className="master-div">
          <div className="top-white-space-div"></div>
          <div className="art" >
            <div className="a">
              <img src={this.state.imageURL1.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL1" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL1.imageid}></img>
            </div>
            <div className="b">
              <img className="larger-image" src={this.state.imageURL2.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL2" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL2.imageid}></img>
            </div>
            <div className="c">
              <img src={this.state.imageURL3.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL3" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL3.imageid}></img></div>
            <div className="d">
              <img className="quad-image" src={this.state.imageURL4.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL4" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL4.imageid}></img>
              <img className="quad-image" src={this.state.imageURL5.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL5" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL5.imageid}></img>
              <img className="quad-image" src={this.state.imageURL6.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL6" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL6.imageid}></img>
              <img className="quad-image" src={this.state.imageURL7.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL7" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL7.imageid}></img>
            </div>
            <div className="e">
              <img src={this.state.imageURL8.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL8" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL8.imageid}></img>
            </div>
            <div className="f">
              <img src={this.state.imageURL9.baseimageurl}
                onClick={() => {
                  this.setState({ whichClick: "imageURL9" })
                  this.setState({ displayZoom: true })
                }}
                alt={this.state.imageURL9.imageid}></img>
            </div>
            <div className="g"><img src={this.state.imageURL10.baseimageurl}
              onClick={() => {
                this.setState({ whichClick: "imageURL10" })
                this.setState({ displayZoom: true })
              }}
              alt={this.state.imageURL10.imageid}></img>
            </div>
          </div>
          <div className="plaque-wording-div">
            <div className="plaque-stand-div">
              <h1 className="courtesy-h1">Courtesy of the Harvard Archives</h1>
              <p className="be-kind-p">Remember to be courteous and respectful of the art and artists displayed here. </p>
              <p className="many-untitled-p">Many of the works featured are unfortunately unknown and untitled</p>
            </div>
            <div className="letter-box-div"><Letter /></div>
          </div>
          <div className="bottom-white-space-div"></div>
        </div>
        <div className='info'>
          {this.checkInfo()}
        </div>
      </div>
    );
  }
}
export default Art;
